<template>
  <div>
    <div class="editDiv el-col el-col-12 el-col-xs-12 el-col-sm-12" style="display:none">
      <el-form ref="form" :model="editData" label-width="80px">
        <el-form-item label="系统名称">
          <el-input v-model="editData.systemName"></el-input>
        </el-form-item>
        <el-form-item label="责任人">
          <el-input v-model="editData.principalUser"></el-input>
        </el-form-item>
        <el-form-item label="系统环境" >
          <div v-for="env in editData.envList" style="text-align:left;">
              <el-input v-model="env.envName" style="width:200px;"></el-input>---<el-input v-model="env.envAddress" style="width:200px;"></el-input>
          </div>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">修改</el-button>
          <el-button @click="onCancel">取消</el-button>
        </el-form-item>
      </el-form>
    </div>
    <el-table
      :data="tableData"
      class="sysmgmTable"
      props="editData"
      border
      style="width: 100%">
      <el-table-column
        label="系统code"
        width="180">
        <template scope="scope">
          <span style="margin-left: 10px">{{ scope.row.systemCode }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="系统名称"
        width="180">
        <template scope="scope">
          <span style="margin-left: 10px">{{ scope.row.systemName }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="负责人"
        width="180">
        <template scope="scope">
          <span style="margin-left: 10px">{{ scope.row.principalUser }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="环境列表"
        width="180">
        <template scope="scope">
          <ul>
            <li v-for="env in scope.row.envList">
              {{ env.envName }}:{{ env.envAddress }}
            </li>
          </ul>
        </template>
      </el-table-column>
      <el-table-column
        label="更新时间"
        width="180">
        <template scope="scope">
          <span style="margin-left: 10px">{{ scope.row.gmtCreate }}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template scope="scope">
          <el-button @click="handleEdit(scope.$index, scope.row)" size="small" >编辑</el-button>

          <el-button
            size="small"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import axios from 'axios';
  import EditForm from '@/components/EditForm'



  export default {
    data: () => ({
    tableData: [],
      errors: [],
    editData:{}
    }),
    created() {
      axios.get(`http://jsonplaceholder.typicode.com/posts`)
        .then(response => {
        // JSON responses are automatically parsed.

        this.tableData =[{
          "envList": [
            {
              "id": 0,
              "systemCode": "",
              "envName": "正式环境",
              "envAddress": "10.2.2.1",
              "gmtCreate": "",
              "gmtModify": ""
            },
            {
              "id": 0,
              "systemCode": "",
              "envName": "正式环境2",
              "envAddress": "10.2.2.1",
              "gmtCreate": "",
              "gmtModify": ""
            }
          ],
          "gmtCreate": "2016-2-12",
          "gmtModify": "2019-27-3",
          "id": 0,
          "principalUser": "fuzeren",
          "systemCode": "jkflasjkldf",
          "systemName": "jaajasss"
        }];
    })
    .catch(e => {
        this.errors.push(e)
    })},
    methods: {
      handleEdit(index, row){
        this.editData = row;
        document.getElementsByClassName("editDiv")[0].style.display = "";
        document.getElementsByClassName("sysmgmTable")[0].style.display = "none";
      },
      handleDelete(index, row) {
        axios.post('/system/delete?systemCode='+row.systemCode)
          .then(function(response){
            console.log('saved successfully');
          })
          .catch(function (error) {
            console.log(error);
          });
      },
      onSubmit() {
        axios.post('/system/save', { data:this.editData })
          .then(function(response){
            console.log('saved successfully');
          })
          .catch(function (error) {
            console.log(error);
          });
      },
      onCancel(){
        document.getElementsByClassName("editDiv")[0].style.display = "none";
        document.getElementsByClassName("sysmgmTable")[0].style.display = "";
      }
    }}
</script>

<style>
  .editDiv{margin-top:20px;}
</style>



